//
// Index style sheet
// sass-lang.com
//

#index
{
    background-image: url("../../assets/toulouse.jpg");
    background-size: cover;
    .title {
        background-color: #C4D7ED;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 30px;
        margin-top: 30px;
        height: 100px;
        width: 1100px;
        img {
            width: inherit;
            height: inherit;
        }
    }

    .content{
        background-color: #C4D7ED;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        max-width: 1500px;
        font-size: 35px;
        font-family: "Raleway";

        .container {
            img{
                width: 675px;
                border: 1px solid gray;
                height: max-content;
                
            }
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
        margin-bottom: 10px;
        }

        .text{
            max-width: 675px;
            text-align: justify;
            font-size: 25px;
            font-family: "OpenSans";
        }
        
        .access{
            margin-bottom: 30px;
        }
    }
}
